*,
*::before,
*::after {
  box-sizing: border-box;
}

body {
  background-color: lightgray;
}

/* On screens that are 576px wide or less, apply this styling */
@media screen and (max-width: 576px) {
  .container {
    width: 95%;
  }
  /* Align card text */
  .card-text {
    text-align: justify;
  }

  /* Size bio image */
  .bio-image {
    height: auto;
    width: 150px;
    text-align: center;
    display: block;
    justify-content: center;
    align-items: center;
    margin: auto;
    width: 50%;
  }
}

/* Styling for buttons */
.btn {
  background-color: white;
  color: #f89ac6;
  font-weight: bold;
}
